.header {
  max-width: 1920px;
  box-sizing: border-box;
  background-color: #1c1c21;
  margin: auto;
  height: 60px;
}

.title {
  color: white;
  font-size: 36px;
  cursor: pointer;
  align-self: center;
}

.wrapper {
  display: flex;
  padding: 0 150px;
  height: 100%;
  justify-content: space-between;
  align-content: center;
}
